.reveal {
	padding: $padding-large;
	.close-button {
		position: absolute;
		top: 0.5rem;
		right: 0.5rem;
		font-size: 3rem;
		color: $miami-blue;
		height: 3rem;
		width: 3rem;
		border-radius: 2rem;
		background-color: rgba(255, 255, 255, 1); }
	&.video {
		padding: 0; }
	&.full iframe {
		width: 100%;
		height: 100%;
		max-height: 100%; } }




